<template>
  <div>
    <v-list>
      <v-list-item-group color="primary">
        <div v-for="(item, i) in items"
             :key="i"
        >
          <v-list-item
            @click="()=>download(item.url)"
          >
            <v-list-item-icon>
              <v-icon v-text="item.icon"></v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="item.fileName"></v-list-item-title>
            </v-list-item-content>
            <v-list-item-action>
              <v-icon>mdi-download</v-icon>
            </v-list-item-action>
          </v-list-item>
          <v-divider></v-divider>
        </div>
      </v-list-item-group>
    </v-list>
    <v-snackbar
      v-model="snackbar.show"
      :timeout="snackbar.timeout"
      :color="snackbar.color"
      :top="true"
    >
      {{snackbar.text}}
      <template v-slot:action="{ attrs }">
        <v-btn
          text
          v-bind="attrs"
          @click="snackbar.show = false"
        >
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'ShareList',

  props: {
    dataUrl: String
  },

  data: () => ({
    items: [
      // { fileName: 'Real-Time', icon: 'mdi-clock', url: 'http://172.16.1.27:12345/shares/1' }
      // { fileName: 'Audience', icon: 'mdi-account', url: 'http://172.16.1.27:12345/shares/2' },
      // { fileName: 'Conversions', icon: 'mdi-flag', url: 'http://172.16.1.27:12345/shares/3' }
    ],
    snackbar: {
      show: false,
      text: '',
      color: '',
      timeout: 6000
    }
  }),

  created () {
    this.loadData()
  },

  methods: {
    loadData () {
      axios.get(this.dataUrl).then(response => {
        if (response.status === 200) {
          const data = response.data
          this.$data.items = data
          return
        }
        this.showError(response.statusText)
      }).catch(e => {
        this.showError(e.message)
      })
    },
    download (url) {
      window.open(url)
    },

    showMessage (message) {
      this.snackbar.text = message
      this.snackbar.color = '#2196f3'
      this.snackbar.show = true
    },
    showSuccess (message) {
      this.snackbar.text = message
      this.snackbar.color = '#4caf50'
      this.snackbar.show = true
    },
    showWarning (message) {
      this.snackbar.text = message
      this.snackbar.color = '#ff9800'
      this.snackbar.show = true
    },
    showError (message) {
      this.snackbar.text = message
      this.snackbar.color = '#f44336'
      this.snackbar.show = true
    }
  }
}
</script>
